<template>
	<div class="sub-container">
		<h2 class="txt-center marB-50">비밀번호 찾기</h2>
		<div v-if="step == 1">
			<div class="input-box">
				<div class="user-input">
					<input
						type="email"
						v-model="email"
						placeholder="가입한 이메일"
						autocomplete="off"
					/>
					<span class="msg" id="check_id"></span>
				</div>
				<div v-if="emailSended" class="user-input">
					<input
						type="password"
						v-model="authNum"
						placeholder="인증번호를 입력해주세요"
					/>
					<span class="msg" id="check_pwd"></span>
				</div>
				<button
					v-if="!emailSended"
					class="btn full dark"
					id="login-btn"
					@click="sendAuthNumberForPassword"
				>
					인증번호 보내기
				</button>
				<LoadingSpinner :loading="loading" />
				<NotificationModal
					v-if="isModalShown"
					:msg="modalMsg"
					@changeIsShown="changeIsShown"
				></NotificationModal>
				<button
					v-if="emailSended"
					class="btn full dark"
					@click="checkAuthNumber"
				>
					인증하기
				</button>
			</div>

			<div v-if="emailSended" class="find-box">
				<p @click="sendAuthNumberForPassword">인증 이메일 다시 보내기</p>
			</div>
		</div>
		<div v-if="step == 2" class="found-container">
			<div class="found-items">
				<div class="found-item">
					<p class="body2">비밀번호</p>
					<p>이메일로 임시 비밀번호가 발급되었습니다!</p>
				</div>
			</div>
			<button class="btn" @click="goto('/login')">로그인</button>
		</div>
	</div>
</template>

<script>
import {
	checkAuthNumber,
	sendAuthNumberForPassword,
	sendOnetimePassword,
} from '@/api/user';
import NotificationModal from '@/components/common/NotificationModal';
import LoadingSpinner from '@/components/common/LoadingSpinner';
export default {
	name: 'Login',
	components: { NotificationModal, LoadingSpinner },
	data() {
		return {
			email: '',
			emailSended: false,
			authNum: '',
			isModalShown: false,
			modalMsg: '',
			errMsg: '',
			step: 1,
			authId: '',
			loading: false,
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async sendAuthNumberForPassword() {
			if (!this.email) {
				this.errMsg = '이메일을 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			let user = {
				email: this.email,
			};
			try {
				this.loading = true;
				const { data } = await sendAuthNumberForPassword(user);
				if (data.successYn == 'Y') {
					this.authId = data.authId;
					this.modalMsg = '인증번호가 전송되었습니다!';
					this.emailSended = true;
					this.isModalShown = true;
				} else if (data.successYn == 'N') {
					this.modalMsg = data.msg;
					this.isModalShown = true;
				}
			} catch (e) {
				this.modalMsg = '인증번호 전송에 실패했습니다. 다시 시도해주세요';
				this.isModalShown = true;
			} finally {
				this.loading = false;
			}
		},
		async checkAuthNumber() {
			try {
				let authData = {
					id: this.authId,
					email: this.email,
					authNum: this.authNum,
				};
				this.loading = true;
				let { data } = await checkAuthNumber(authData);
				if (data.result == 'Y') {
					let userData = {
						email: this.email,
					};
					try {
						let { data } = await sendOnetimePassword(userData);
						console.log(data);
						if (data.successYn == 'Y') {
							this.step = 2;
						} else if (data.successYn == 'N') {
							this.modalMsg = '임시 비밀번호 발급에 실패했습니다!';
							this.changeIsShown(true);
						}
					} catch (e) {
						console.log(e);
						this.modalMsg = '임시 비밀번호 발급에 실패했습니다!';
						this.changeIsShown(true);
					} finally {
						this.loading = false;
					}
				} else {
					this.modalMsg = '인증번호가 일치하지 않습니다';
					this.changeIsShown(true);
				}
			} catch (e) {
				console.log(e);
			}
		},
		initForm() {
			(this.id = ''), (this.pw = ''), (this.msg = '');
		},
		changeIsShown(isShown) {
			this.isModalShown = isShown;
		},
	},
};
</script>

<style scoped>
.found-container {
	width: 340px;
	margin: 0 auto;
}
.found-items {
	border-top: 2px solid #e6e6e6;
	border-bottom: 2px solid #e6e6e6;
	padding: 20px 0;
	margin-bottom: 30px;
}
.found-item {
	display: flex;
	align-items: center;
	padding: 0 18px;
}
.found-item p:nth-child(1) {
	width: 60px;
	margin-right: 10px;
	color: #8c8c8c;
}
.found-container button {
	width: 100%;
}
</style>
