<template>
	<ModalView>
		<div class="modal-container">
			<div class="modal-msg">
				<p class="body3">
					{{ msg }}
				</p>
			</div>
			<button @click="closeModal">확인</button>
		</div>
	</ModalView>
</template>

<script>
import ModalView from '@/components/common/ModalView';
export default {
	name: 'NotificationModal.vue',
	components: { ModalView },
	props: {
		msg: {
			type: String,
			required: true,
		},
	},
	data() {
		return {
			isModalShown: true,
		};
	},
	methods: {
		closeModal() {
			this.isModalShown = false;
			this.$emit('changeIsShown', this.isModalShown);
		},
	},
};
</script>

<style scoped>
.modal-msg {
	width: 430px;
	height: 162px;
	display: flex;
	justify-content: center;
	align-items: center;
	border: 2px solid #000;
	box-sizing: border-box;
	border-bottom: 0;
}
.modal-container button {
	width: 100%;
	height: 57px;
	border: 2px solid #000;
	box-sizing: border-box;
	font-family: Black Han Sans;
	font-style: normal;
	font-weight: normal;
	font-size: 14px;
	line-height: 17px;
	background: #00ad84;
}
</style>
